import _ from 'lodash'
import React from 'react'
import * as ROUTES from '../../constants/routes'
import AttachMoney from 'material-ui/svg-icons/editor/attach-money'
import Finance from 'material-ui/svg-icons/action/account-balance-wallet'
import Map from 'material-ui/svg-icons/maps/map'
import Settings from 'material-ui/svg-icons/action/settings'
import Statistics from 'material-ui/svg-icons/action/timeline'
import Store from 'material-ui/svg-icons/action/store'
import Person from 'material-ui/svg-icons/social/group'
import Supply from 'material-ui/svg-icons/action/swap-horiz'
import Products from 'material-ui/svg-icons/device/widgets'
import {getGroups} from '../../helpers/storage'

export const MenuItems = [
    {
        name: '',
        icon: (<AttachMoney />),
        url: ROUTES.ORDER_LIST_URL,
        childs: [
            {name: '', url: ROUTES.ORDER_LIST_URL},
            {name: '', url: ROUTES.ACTIVITY_LIST_URL},
            {name: '', url: ROUTES.PLAN_LIST_URL},
            {name: ' ', url: ROUTES.PRICE_LIST_URL},
            {name: ' ', url: ROUTES.TRACKING_LIST_URL},
            {name: ' ', url: ROUTES.PRICES_LIST_URL},
            {name: '', url: ROUTES.ZONES_LIST_URL}
        ]
    },
    {
        name: '',
        icon: (<Person />),
        url: ROUTES.CLIENT_LIST_URL,
        childs: [
            {name: '', url: ROUTES.CLIENT_LIST_URL},
            {name: '', url: ROUTES.SHOP_LIST_URL}
        ]
    },
    {
        name: '',
        icon: (<Products />),
        url: ROUTES.PRODUCT_LIST_URL,
        childs: [
            {name: '', url: ROUTES.PRODUCT_LIST_URL}
        ]
    },
    {
        name: '',
        icon: (<Store />),
        url: ROUTES.REMAINDER_LIST_URL,
        childs: [
            {name: '', url: ROUTES.REMAINDER_LIST_URL},
            {name: ' / ', url: ROUTES.STOCK_RECEIVE_LIST_URL}
        ]
    },
    {
        name: '',
        icon: (<Supply />),
        url: ROUTES.PROVIDER_LIST_URL,
        childs: [
            {name: '', url: ROUTES.PROVIDER_LIST_URL},
            {name: '', url: ROUTES.SUPPLY_LIST_URL}
        ]
    },
    {
        name: '',
        icon: (<Finance />),
        url: ROUTES.TRANSACTION_LIST_URL,
        childs: [
            {name: '', url: ROUTES.TRANSACTION_LIST_URL},
            {name: ' ', url: ROUTES.PENDING_EXPENSES_LIST_URL},
            {name: 'e ', url: ROUTES.PENDING_PAYMENTS_LIST_URL},
            {name: ' ', url: ROUTES.CLIENT_BALANCE_LIST_URL}
        ]
    },
    {
        name: '',
        icon: (<Map />),
        url: ROUTES.MANUFACTURE_CUSTOM_URL,
        childs: [
            {name: '', url: ROUTES.MANUFACTURE_CUSTOM_URL}
        ]
    },
    {
        name: '',
        icon: (<Statistics />),
        url: ROUTES.STATISTICS_LIST_URL,
        childs: [
            {name: '', url: ROUTES.STATISTICS_LIST_URL}
        ]
    },
    {
        name: '',
        icon: (<Settings />),
        url: ROUTES.STOCK_LIST_URL,
        bottom: true,
        childs: [
            {name: '', url: ROUTES.STOCK_LIST_URL},
            {name: '', url: ROUTES.CURRENCY_LIST_URL},
            {name: '', url: ROUTES.CASHBOX_LIST_URL},
            {name: ' ', url: ROUTES.EXPENSIVE_CATEGORY_LIST_URL},
            {name: ' ', url: ROUTES.PRODUCT_TYPE_LIST_URL},
            {name: '', url: ROUTES.USERS_LIST_URL},
            {name: '', url: ROUTES.MEASUREMENT_LIST_URL},
            {name: '', url: ROUTES.EQUIPMENT_LIST_URL},
            {name: '', url: ROUTES.SHIFT_LIST_URL},
            {name: ' ', url: ROUTES.MARKET_TYPE_LIST_URL},
            {name: '', url: ROUTES.POSITION_LIST_URL}
        ]
    }
]

const groups = [
    {
        id: 1,
        name: 'SupDir',
        urls: [
            ROUTES.REMAINDER_LIST_URL,
            ROUTES.STOCK_RECEIVE_LIST_URL
        ]
    },
    {
        id: 2,
        name: 'delivery',
        urls: []
    },
    {
        id: 3,
        name: 'agent',
        urls: []
    },
    {
        id: 4,
        name: 'merch',
        urls: []
    },
    {
        id: 5,
        name: 'collector'
    },
    {
        id: 6,
        name: 'cashier',
        urls: [
            ROUTES.TRANSACTION_LIST_URL,
            ROUTES.PENDING_EXPENSES_LIST_URL,
            ROUTES.PENDING_PAYMENTS_LIST_URL,
            ROUTES.CLIENT_BALANCE_LIST_URL
        ]
    },
    {
        id: 7,
        name: 'supervisor'
    }
]

const sessionGroups = getGroups()
let menus = []
const getLinksByGroup = (groupId) => {
    return _.get(_.find(groups, {'id': _.toInteger(groupId)}), 'urls')
}
_.map(sessionGroups, (item) => {
    const links = getLinksByGroup(item)
    _.map(links, (link) => {
        menus.push(link)
    })
})



