const stockTypeFormat = (type) => {
    if (type === 'supply') {
        return ''
    } else if (type === 'transfer' || type === 'stock_transfer') {
        return ''
    } else if (type === 'order transfer product') {
        return ' '
    } else if (type === 'order_return') {
        return ''
    }
    return type
}

export default stockTypeFormat



